.pg-bar {
	margin: 15px;
	margin-left: 50px;
	border-radius: 30px;
	position: relative;
	left: 80px;
	-webkit-box-shadow: 5px 5px 2.5px #888888;
	        box-shadow: 5px 5px 2.5px #888888;
	height: 30px;
	width: 60%;

}

.proficiency {
	position: relative;
	top: 40px;
	right: -60%;
	margin: 20px;
	border: 2px solid #43a5e6;
	border-radius: 15px;
	background-color: #ffffff;
	-webkit-box-shadow: 10px 10px 5px #888888;
	        box-shadow: 10px 10px 5px #888888;
	overflow:auto;
	min-height: 400px;
	max-width: 400px;

}

.about {
	margin: 30px;
}

.article {
	position: relative;
	top: 30px;
}

span {
	display: inline;
}

.bar-title {
	font-size: 22px;
	position: relative;
	top: 50px;
	left: 40px;

}



